/* Estilos generales para la página */
#fuel-calculator {
    max-width: 100%; /* Ajusta el ancho a 100% */
    margin: 0 auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
    font-family: 'Arial', sans-serif;
}

/* Título principal */
#fuel-calculator h2 {
    text-align: center;
    font-size: 34px;
    color: #4A90E2;
    margin-bottom: 20px;
}

/* Estilo para los formularios */
form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

/* Estilo para los campos de entrada y selección */
form div {
    margin-bottom: 0px; /* Reducir el espacio entre las filas */
}

label {
    font-size: 1rem;
    color: #555;
    margin-bottom: 5px;
    display: inline-block;
}

/* Aseguramos que cada campo ocupe el mismo espacio */
input, select, label {
    margin-bottom: 5px; /* Reducir el margen inferior de cada campo */
    padding: 6px; /* Reducir padding para ahorrar espacio */
    font-size: 0.9rem; /* Ajustar tamaño de la fuente */
}

input:focus, select:focus {
    border-color: #4A90E2;
    box-shadow: 0 0 5px rgba(74, 144, 226, 0.3);
    outline: none;
}

/* Alineación de campos de km y precio por galón en la misma línea */
.unit-section, .inline-fields, .route-company-container {
    display: flex;
    gap: 15px; /* Reducido para menos espacio */
}

.unit-section div, .inline-fields div, .route-company-container div {
    flex: 1;
}

/* Estilos para los resultados redondeados */
#fuel-calculator .rounded-result {
    font-size: 20px;
    color: #333;
    text-align: center;
    margin-top: 10px;
}

/* Estilos para la sección de precio por galón */
input[name="pricePerGallon"] {
    padding-left: 35px;
    background-image: url('https://upload.wikimedia.org/wikipedia/commons/5/5f/Guatemala_symbol.png');
    background-repeat: no-repeat;
    background-position: 10px center;
    background-size: 20px 20px;
}

/* Sección de fecha y hora */
.date-time {
    position: fixed;
    bottom: 10px;
    right: 10px;
    font-size: 14px;
    color: #333;
}

.date-time-container {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.date-time-container input {
    width: 100%;
    padding: 8px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* Ajuste para el campo de fecha congelada debajo de piloto */
.pilot-date-time {
    margin-top: 10px;
    display: flex;
    flex-direction: column;
}

.pilot-date-time button {
    margin-top: 10px;
}

/* Responsividad para dispositivos pequeños */
@media (min-width: 768px) {
    #fuel-calculator .form-row {
        flex-direction: row;
    }

    .unit-section div, .inline-fields div, .route-company-container div {
        width: 100%; /* Utilizar el 100% del ancho */
    }

    input, select, label {
        font-size: 0.9rem;
        padding: 6px;
        margin: 0;
    }

    button {
        padding: 8px 14px;
        font-size: 1rem;
    }

    button + button {
        margin-top: 8px;
    }
}

@media (max-width: 480px) {
    input, select {
        padding: 10px;
        font-size: 0.9rem;
    }

    button {
        padding: 10px;
        font-size: 0.9rem;
    }

    label {
        font-size: 0.9rem;
    }
}

#fuel-calculator .form-row {
    display: flex;
    flex-direction: column; /* Cambiar a columna para pantallas pequeñas */
    gap: 8px;
    margin-bottom: 0;
}

#fuel-calculator input, #fuel-calculator select, #fuel-calculator label {
    margin-bottom: 2px !important;
    padding: 4px !important;
}

table {
    width: 100%;
    border-collapse: collapse;
    margin-bottom: 20px;
}

td {
    padding: 8px;
    vertical-align: middle;
}

label {
    font-weight: bold;
}

input, select {
    width: 100%;
    padding: 5px;
    font-size: 1rem;
    border: 1px solid #ddd;
    border-radius: 5px;
}

button {
    margin: 5px 5px 0 0;
    padding: 10px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}

/* Estilo independiente para el label de Piloto */
#piloto-label {
    font-size: 1rem;
    font-weight: bold;
    color: #000;
    display: block;
    margin-bottom: 5px;
    word-wrap: break-word;
    white-space: normal;
}

/* Estilo para el campo select (piloto) */
#piloto-select {
    width: 100%;
    padding: 8px;
    font-size: 1rem;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    appearance: none;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

/* Estilo para el texto dentro del select (opciones) */
#piloto-select option {
    white-space: nowrap;
}

@media (max-width: 480px) {
    #piloto-select {
        font-size: 0.9rem;
    }
}


/* Estilos generales para dispositivos pequeños */
@media (max-width: 480px) {
    /* Asegura que los campos sean más legibles y ocupen todo el ancho */
    input, select {
        width: 100%; /* Ocupa todo el ancho disponible */
        font-size: 1rem; /* Aumenta el tamaño de fuente */
        padding: 10px; /* Espaciado interno mayor */
        border-radius: 8px; /* Bordes redondeados para mejor diseño */
    }

    /* Ajustar los labels para mejor visualización */
    label {
        font-size: 1rem; /* Aumenta el tamaño de los labels */
        margin-bottom: 8px; /* Espacio entre label y campo */
    }

    /* Botones más grandes para facilitar su uso */
    button {
        padding: 12px;
        font-size: 1rem;
        width: 100%; /* Botones ocuparán todo el ancho disponible */
    }

    /* Asegura que los contenedores con múltiples campos se ajusten en filas */
    .unit-section, .inline-fields, .route-company-container {
        flex-wrap: wrap; /* Permite que los elementos bajen de línea si es necesario */
        gap: 10px; /* Espaciado entre campos */
    }

    .unit-section div, .inline-fields div, .route-company-container div {
        width: 100%; /* Los campos ocuparán toda la línea */
    }

    /* Ajustes adicionales para tablas */
    table {
        font-size: 0.9rem; /* Reduce ligeramente el tamaño del texto */
        overflow-x: auto; /* Agrega scroll horizontal si es necesario */
    }

    td, th {
        padding: 10px; /* Mayor espacio interno para las celdas */
    }
}
