/* Estilos generales para la página */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f9;
    color: #333;
  }
  
  /* Contenedor principal */
  div {
    max-width: 800px;
    margin: 20px auto;
    padding: 20px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    border-radius: 8px;
  }
  
  /* Título principal */
  h2 {
    text-align: center;
    font-size: 24px;
    color: #4A90E2;
    margin-bottom: 20px;
  }
  
  /* Estilos para la lista de registros */
  ul {
    list-style-type: none;
    padding: 0;
  }
  
  li {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #fafafa;
    transition: background-color 0.3s;
  }
  
  li:hover {
    background-color: #f0f8ff;
  }
  
  /* Estilos para las etiquetas (labels) */
  p {
    margin: 5px 0;
    font-size: 14px;
  }
  
  /* Resaltar los títulos */
  strong {
    font-weight: bold;
    color: #4A90E2;
  }
  
  /* Botones de acción */
  button {
    padding: 8px 15px;
    margin: 5px;
    font-size: 14px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    background-color: #4A90E2;
    color: white;
    transition: background-color 0.3s;
  }
  
  button:hover {
    background-color: #357ABD;
  }
  
  /* Estilo para el botón de regresar */
  button:nth-of-type(2) {
    background-color: #ccc;
  }
  
  button:nth-of-type(2):hover {
    background-color: #999;
  }
  

  /* Estilo para los campos de filtro */
.filters {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
  }
  
  .filters input {
    padding: 8px;
    font-size: 14px;
    width: 48%;
    border-radius: 5px;
    border: 1px solid #ddd;
  }
  

  .export-excel {
    padding: 8px 12px;
    background-color: #4caf50;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin-top: 10px;
  }
  
  .export-excel:hover {
    background-color: #45a049;
  }

  